export * from '@draftout/draftout/services/helpers';

export {referenceError, syntaxError, typeError} from '@draftout/draftout/lib/logger.m';

export const noop = () => {};

export const {cancelIdleCallback, requestIdleCallback} = ((window: Window) => {
  let {cancelIdleCallback, requestIdleCallback} = window;

  if (cancelIdleCallback && requestIdleCallback) {
    cancelIdleCallback = cancelIdleCallback.bind(window);
    requestIdleCallback = requestIdleCallback.bind(window);
  } else {
    const idleCallbackDeadline = Object.freeze({timeRemaining: () => 0});
    cancelIdleCallback = (handle: any) => clearTimeout(handle);
    requestIdleCallback = (callback: any, options: any) =>
      setTimeout(
        () => callback(idleCallbackDeadline),
        (options && options.timeout > 0 && options.timeout) || 0,
      );
  }

  return {cancelIdleCallback, requestIdleCallback};
})((typeof window === 'object' && window) || ({} as any));


export const reduceAsync = async <T, U>(
  functions: Iterable<(a: T | U) => Awaitable<U | undefined>>,
  value?: T | U,
  exceptions?: Error[] | false,
) => {
  if (exceptions || exceptions === false) {
    for (const Æ’ of functions) {
      try {
        const result = await Æ’(value);
        value = result === undefined ? value : result;
      } catch (exception) {
        exceptions && (exception.stack, exceptions.push(exception));
      }
    }
  } else {
    for (const Æ’ of functions) {
      const result = await Æ’(value);
      value = result === undefined ? value : result;
    }
  }
  return value;
};

export type Awaitable<T> = T | Promise<T>;

export const gracefully = async <T>(promise: Promise<T>) => {
  try {
    return await promise;
  } catch (exception) {
    console.warn(exception);
  }
}



