import {Element, html, SHADOW_ROOT_OPEN} from 'perpetui';

export class MarkoutToolbar extends Element {

  static readonly tagName = 'markout-toolbar';

  static shadowRoot = SHADOW_ROOT_OPEN;

  // static stylesheet = html`<style></style>` as HTMLStyleElement;

  static template = html`
    <template>
      <div>
        <slot></slot>
      </div>
    </template>
  ` as HTMLTemplateElement;

  constructor() {
    super();
    // arguments.length && console.log(`%O.constructor(${Array(arguments.length).fill('%o').join(', ')})`, ...arguments);
  }
  // static stylesheet = stylesheet;
}

/// AUTO-REGISTER ///
MarkoutToolbar.register(MarkoutToolbar.tagName);

/// AMBIENT ///
declare global {
  namespace JSX {
    interface IntrinsicElements {
      [MarkoutToolbar.tagName]: this['div'] & {[name: string]: any};
    }
  }
}





