/**
 * Drops values occuring within a preset timespan, passing only the first and
 * last values, irrespective of the actual number of intermediate values or the
 * time lapse between the first and the last values in each frame.
 *
 * @param Æ’
 * @param rating
 */
export const throttled = (Æ’, rating = 100) => {
  let last, timeout: any; // = setTimeout(, delay);
  const debouncer = () => (last === undefined || Æ’(last), (timeout = last = undefined));
  return (value = null) => {
    (last = timeout && value) === undefined && (timeout = setTimeout(debouncer, rating)) && Æ’(value);
  };
};

/**
 * Drops values occuring within a preset timespan, passing only the last value,
 * irrespective of the actual number of values previously occuring within the
 * each frame.
 * @param Æ’
 * @param latency
 */
export const debounced = (Æ’, latency = 100) => {
  // TODO: Debounce is not working yet
  let last, timeout: any; // = setTimeout(, delay);
  const debouncer = () => (last === undefined || Æ’(last), (timeout = last = undefined));
  return (value = null) => {
    (last = timeout && value) === undefined && (timeout = setTimeout(debouncer, latency));
  };

}

export const resized = Stream.from();
window.addEventListener('resize', throttled(resized, 10), {passive: true});

