import {schemas} from '@draftout/draftout/lib/models.m';
import {Controller, All, ID} from '@draftout/draftout/models/lib/controller';
import {ClientController} from '@draftout/draftout/models/lib/controller/client';
import * as helpers from './helpers';
import * as logger from '@draftout/draftout/lib/logger.m';

export type schemas = typeof schemas;
export type controllers = {[k in keyof schemas]: Controller<any>};
export const controllers: controllers = {} as controllers;

let draftID, draftQuery;

const initialize = {
  Draft: async (controller: ClientController<any>) => {
    const query = draftID || {}; // author: 'dummy'
    try {
      const result = await (draftID ? controller.get(draftID) : controller.get(All));
      const draft = (result && result[0]) || result || undefined;
      console.log('Loaded draft %O: %o', query, draft);
    } catch (exception) {
      console.warn('Failed to load draft %O: %O', query, exception);
    }
  },
};

for (const ref in schemas) {
  const controller = new ClientController(`http://localhost:8080/${ref}`.toLowerCase());
  controllers[ref] = controller;
  // ref in initialize && initialize[ref](controller);
  // controller.get(All)
  //   .catch(console.warn)
  //   .then(console.log)
}

export interface DraftoutService {
  schemas: schemas;
  controllers: controllers;
  import: typeof helpers.dynamicImport;
  deferred: typeof helpers.deferred;
  logger: typeof logger;
}

export const DraftoutService: DraftoutService = {
  schemas,
  controllers,
  import: helpers.dynamicImport,
  deferred: helpers.deferred,
  logger,
};

console.log('DraftoutService: %o', DraftoutService);

export default DraftoutService;
