import PropTypes, {oneOf} from 'prop-types';
import React, {HTMLProps} from 'react';
import {requestIdleCallback} from '../services/helpers';
import {resized} from '../services/Events';

/// Services
import {MarkoutEditorService} from '../services/MarkoutEditorService';

/// Types
import {Container, Editor} from '../services/MarkoutEditorService';
import {EditorOptions, TextOptions, EditorModel} from '@draftout/draftout/types/monaco/editor';

const {create: createEditor, destroy: destroyEditor} = MarkoutEditorService;

class MarkoutEditor extends React.Component<Props, State> {
  element: Container;
  editor?: Editor;
  updates?: Props;
  state: State = {};

  inject = async (element: Container) => {
    const currentElement = this.element;

    if (element === currentElement) return;

    currentElement && destroyEditor(currentElement);

    let editor: Editor = (this.editor = undefined);

    (editor = this.editor = await createEditor(element)) &&
      (this.element = element) &&
      (this.update(this.props), this.layout());
    this.setState({editor, element});
  };

  layout = () => {
    const editor = this.editor;
    const editorElement = this.element;

    if (!editor || !editorElement) return;

    const containerElement = editorElement.parentElement;
    const bounds = (containerElement || editorElement).getBoundingClientRect();
    const width = bounds.width;
    const height = bounds.height;
    editor.layout({width, height});
    // console.log('layout %o', {width, height, bounds, editor, container});
  };

  updateOptions = () => {
    const options = this.updates;
    const editor = this.editor;
    if (!options || !editor) return;
    this.updates = undefined;
    editor.updateOptions(options);
    const setters = MarkoutEditorService.setters;
    for (const k in setters) k in options && setters[k](options[k], editor);
  };
  setOptions = (options = this.updates) => {
    this.updates = undefined;
    const editor = this.editor; //  || this.state.editor
    if (!options || !editor) return;
    editor.updateOptions(options);
    const setters = MarkoutEditorService.setters;
    for (const k in setters) k in options && setters[k](options[k], editor);
  };

  update = (options: Props, previous: Props = this.props) => {
    const editor = this.editor;
    if (!editor) return;
    // this.layout();
    this.updates = options;
    requestIdleCallback(this.updateOptions);
  };

  componentWillReceiveProps(props: Props) {
    this.editor && this.update(props, this.props);
  }

  componentWillMount() {
    resized.map(this.layout);
    // requestIdleCallback(this.layout);
    // window.addEventListener('resize', this.layout, {passive: true});
  }

  componentWillUnmount() {
    resized.unmap(this.layout);
  }

  render() {
    const {inject} = this;
    return (
      <div className="MarkoutEditorContainer">
        <div className="MonacoEditorContainer" ref={inject as any} />
      </div>
    );
  }
}

namespace MarkoutEditor {
  import oneOfType = PropTypes.oneOfType;
  import arrayOf = PropTypes.arrayOf;
  import string = PropTypes.string;
  import number = PropTypes.number;
  import boolean = PropTypes.bool;
  import callback = PropTypes.func;
  import object = PropTypes.shape;
  import objectOf = PropTypes.objectOf;

  export const EditorOptions = {
    ariaLabel: string,
    rulers: arrayOf(number),
    wordSeparators: string,
    selectionClipboard: boolean,
    lineNumbers: oneOfType([oneOf(['on', 'off', 'relative', 'interval']), callback]),
    selectOnLineNumbers: boolean,
    lineNumbersMinChars: number,
    glyphMargin: boolean,
    lineDecorationsWidth: oneOfType([number, string]),
    revealHorizontalRightPadding: number,
    roundedSelection: boolean,
    extraEditorClassName: string,
    readOnly: boolean,
    fixedOverflowWidgets: boolean,
    overviewRulerLanes: number,
    overviewRulerBorder: boolean,
    cursorBlinking: string,
    mouseWheelZoom: boolean,
    cursorStyle: string,
    cursorWidth: number,
    fontLigatures: boolean,
    disableLayerHinting: boolean,
    disableMonospaceOptimizations: boolean,
    hideCursorInOverviewRuler: boolean,
    scrollBeyondLastLine: boolean,
    smoothScrolling: boolean,
    automaticLayout: boolean,
    wordWrap: oneOf(['off', 'on', 'wordWrapColumn', 'bounded']),
    wordWrapColumn: number,
    wordWrapMinified: boolean,
    wrappingIndent: string,
    wordWrapBreakBeforeCharacters: string,
    wordWrapBreakAfterCharacters: string,
    wordWrapBreakObtrusiveCharacters: string,
    stopRenderingLineAfter: number,
    hover: boolean,
    links: boolean,
    colorDecorators: boolean,
    contextmenu: boolean,
    mouseWheelScrollSensitivity: number,
    multiCursorModifier: oneOf(['ctrlCmd', 'alt']),
    multiCursorMergeOverlapping: boolean,
    accessibilitySupport: oneOf(['auto', 'off', 'on']),
    quickSuggestions: oneOfType([
      boolean,
      object({
        other: boolean,
        comments: boolean,
        strings: boolean,
      }),
    ]),
    quickSuggestionsDelay: number,
    parameterHints: boolean,
    iconsInSuggestions: boolean,
    autoClosingBrackets: boolean,
    autoIndent: boolean,
    formatOnType: boolean,
    formatOnPaste: boolean,
    dragAndDrop: boolean,
    suggestOnTriggerCharacters: boolean,
    acceptSuggestionOnEnter: oneOfType([boolean, oneOf(['on', 'smart', 'off'])]),
    acceptSuggestionOnCommitCharacter: boolean,
    snippetSuggestions: oneOf(['top', 'bottom', 'inline', 'none']),
    emptySelectionClipboard: boolean,
    wordBasedSuggestions: boolean,
    suggestSelection: oneOf(['first', 'recentlyUsed', 'recentlyUsedByPrefix']),
    suggestFontSize: number,
    suggestLineHeight: number,
    selectionHighlight: boolean,
    occurrencesHighlight: boolean,
    codeLens: boolean,
    codeActionsOnSaveTimeout: number,
    folding: boolean,
    foldingStrategy: oneOf(['auto', 'indentation']),
    showFoldingControls: oneOf(['always', 'mouseover']),
    matchBrackets: boolean,
    renderWhitespace: oneOf(['none', 'boundary', 'all']),
    renderControlCharacters: boolean,
    renderIndentGuides: boolean,
    renderLineHighlight: oneOf(['none', 'gutter', 'line', 'all']),
    useTabStops: boolean,
    fontFamily: string,
    fontWeight: oneOf([
      'normal',
      'bold',
      'bolder',
      'lighter',
      'initial',
      'inherit',
      '100',
      '200',
      '300',
      '400',
      '500',
      '600',
      '700',
      '800',
      '900',
    ]),
    fontSize: number,
    lineHeight: number,
    letterSpacing: number,
    scrollbar: object({
      // IEditorScrollbarOptions
      arrowSize: number,
      vertical: string,
      horizontal: string,
      useShadows: boolean,
      verticalHasArrows: boolean,
      horizontalHasArrows: boolean,
      handleMouseWheel: boolean,
      horizontalScrollbarSize: number,
      verticalScrollbarSize: number,
      verticalSliderSize: number,
      horizontalSliderSize: number,
    }),

    minimap: object({
      // IEditorMinimapOptions
      enabled: boolean,
      side: oneOf(['right', 'left']),
      showSlider: oneOf(['always', 'mouseover']),
      renderCharacters: boolean,
      maxColumn: number,
    }),
    find: object({
      // IEditorFindOptions
      seedSearchStringFromSelection: boolean,
      autoFindInSelection: boolean,
    }),
    lightbulb: object({
      // IEditorLightbulbOptions
      enabled: boolean,
    }),
    codeActionsOnSave: objectOf(
      // ICodeActionsOnSaveOptions
      boolean,
    ),
  };

  export const TextOptions = {
    tabSize: number,
    insertSpaces: boolean,
    trimAutoWhitespace: boolean,
  };

  export const Props = {
    value: PropTypes.string,
    language: PropTypes.string,
    theme: PropTypes.string,
    ...EditorOptions,
  };

  export const defaultProps: Props = {
    language: 'typescript',
  };

  export const propTypes = Props;

  export function setOptions(editor: Editor, options: Props, previous?: Props) {
    editor.updateOptions(options);
    const setters = MarkoutEditorService.setters;
    for (const k in setters) k in options && setters[k](options[k], editor);
  }
}

declare namespace MarkoutEditor {
  export type Options<T = EditorOptions & TextOptions> = {[K in keyof T]?: T[K]};

  export type Language = string;
  export type Theme = string;

  export interface Props extends Options, HTMLProps<HTMLDivElement> {
    value?: string;
    language?: Language;
    theme?: Theme;
  }

  export interface State {
    editor?: Editor;
    element?: Container;
  }
}

import defaults = MarkoutEditor.defaultProps;
import Props = MarkoutEditor.Props;
import State = MarkoutEditor.State;

export default MarkoutEditor;



