import MonacoEditorService, {
  MonacoEnvironment,
} from '@draftout/draftout/services/MonacoEditorService';
import {EditorModel} from '@draftout/draftout/types/monaco/editor'; // EditorOptions, TextOptions,
import DraftoutService from './DraftoutService';
import {reduceAsync, Awaitable, gracefully} from './helpers';

//* SEE: Monaco
//*   API:https://microsoft.github.io/monaco-editor/api/
//*   Playground: https://microsoft.github.io/monaco-editor/playground.html
//*     Exposed Colors: #customizing-the-appearence-exposed-colors
//* SEE: Issues
//*   Disable Keybinding: https://github.com/Microsoft/monaco-editor/issues/287
//*   Editor Service: https://github.com/Microsoft/monaco-editor/issues/291
//*   Persistent Markers: https://github.com/Microsoft/monaco-editor/issues/938
//*   deltaDecorations: https://github.com/Microsoft/monaco-editor/issues/913

const debugging = true;
const id = 'MarkoutEditorService';
const prepend = (args: any[]) => {
  const arg0 = args[0];
  if (typeof arg0 === 'string') {
    args[0] = `[${id}]${(arg0.trim() && ` ${arg0}`) || ''}`;
    return args;
  }

  return [`[${id}]`].concat(args);
};
const warn = (...args: any[]) => console.warn(...prepend(args));
const log = (...args: any[]) => console.warn(...prepend(args));

const {runtimeError} = DraftoutService.logger;

export namespace MarkoutEditorService {
  const defaultBase = '/node_modules/monaco-es/dist';
  const defaultEnvironment = '/lib/monaco-environment.js';
  const editors = new WeakMap<Container, Editor>();
  const configurers = new Set<Configurer>();
  const environment = MonacoEditorService.environment;

  export declare let monaco: MonacoEditorService.Monaco;

  async function initialize() {
    if (MonacoEditorService.monaco) return MonacoEditorService.monaco;
    if (!configurers.size) return MonacoEditorService.load();
    const exceptions = [];
    try {
      const hooks = [...configurers];
      configurers.clear();
      await reduceAsync(hooks, environment, exceptions);
      return MonacoEditorService.load();
    } finally {
      await MonacoEditorService.loaded;
      if (exceptions && exceptions.length)
        warn(`initialize: ${'\n\n%O'.repeat(exceptions.length)}`, ...exceptions);
      else if (debugging && debugging['initialized'] !== false) log(`initialized`);
    }
  }

  // const loadMode = language => gracefully(DraftoutService.import(`${base}/languages/ts/ts.js`));

  export const setters = {
    theme: (theme: string, target?: Editor | EditorModel) => {
      if (MarkoutEditorService.monaco) {
        return MarkoutEditorService.monaco.editor.setTheme(theme);
      }
    },
    language: (language: string, target?: Editor | EditorModel) => {
      if (target) {
        const model = 'getModel' in target ? target.getModel() : target;
        console.log('setLanguage %o', {language, target, model});
        return MarkoutEditorService.monaco.editor.setModelLanguage(model, language);
      }
    },
  };

  /**
   * Configures and loads Monaco from a specified or the default base.
   */
  export async function bootstrap(base: string = defaultBase) {
    const sourceURL = `${base}/monaco.js`;
    if (MonacoEditorService.monaco) {
      return warn('bootstrap: Aborting since monaco seems to be loaded already.');
    }

    const importedEnvironment: Promise<any> = gracefully(
      DraftoutService.import(defaultEnvironment || `${base}/environment.js`),
    ).then(
      (imported: any) =>
        (imported &&
          ((typeof (imported = imported.environment || imported.default || imported) ===
            'function' &&
            imported(base)) ||
            imported)) ||
        undefined,
    );

    MarkoutEditorService.configure(async environment => {
      const imported = await importedEnvironment;
      Object.assign(environment, imported);
      environment.sourceURL = sourceURL;
      return environment;
      // const initial = {...environment};
      // const final = Object.assign(environment); // imported
      // const imported = await importedEnvironment;
      // const scope = (typeof global === 'object' && global) || (typeof self === 'object' && self);
      // const scoped =
      //   (scope && scope['MonacoEnvironment']) ||
      //   (typeof MonacoEnvironment === 'object' && MonacoEnvironment);
      // // const scoped = typeof MonacoEnvironment === 'object' && MonacoEnvironment || (typeof self === 'object' && self.MonacoEnvironment;
      // console.log('environment: %o', {initial, imported, final, scoped});
    });

    // await MonacoEditorService.load(`${base}/monaco.js`);
    gracefully(DraftoutService.import(`${base}/languages/ts/ts.js`));
    return MarkoutEditorService;
  }

  /**
   * Creates a new Markout editor once monaco is ready, by checking if monaco
   * was externally loaded into the global scope, or by attempting to load it
   * from the sourceURL property of the self.MonacoEnvironment
   *
   * @param container HTML container element for editor
   */
  export async function create(container: Container, options?: Options) {
    MonacoEditorService.monaco || (await initialize());

    let editor = editors.get(container);

    if (!editor) {
      editor = MonacoEditorService.monaco.editor.create(container, options);
      editor.onDidDispose(() => editors.delete(container));
      // TODO: Consider life cycle hooks for container
      editors.set(container, editor);
    }

    return editor;
  }

  /**
   * Disposes of the Markout editor instance if one was created for the
   * container element.
   * @param container HTML container element for editor.
   */
  export async function destroy<T extends Container>(container: T) {
    let editor = editors.get(container);
    if (editor) {
      editor.dispose();
      editors.delete(container);
    }
    return container;
  }

  /**
   * Attaches a preload configuration handler to setup the MonacoEnvironment
   * right before loading monaco.
   */
  export async function configure(configurer: Configurer) {
    configurers.add(configurer);
  }

  export type Configurer = (
    environment?: MonacoEnvironment,
  ) => Awaitable<Partial<MonacoEnvironment> | void>;

  export interface Options extends MonacoEditorService.ConstructorOptions {}
  export type Editor = MonacoEditorService.Editor;
  export type Container = MonacoEditorService.Container;
}

Object.defineProperty(MarkoutEditorService, 'monaco', {
  get: () => MonacoEditorService.monaco,
});

export import Editor = MarkoutEditorService.Editor;
export import Container = MarkoutEditorService.Container;
export import Options = MarkoutEditorService.Options;
import {format} from 'path';

export default MarkoutEditorService;

